import * as React from 'react';
import PropTypes from 'prop-types';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import PhotosBackYard from './PhotosBackYard';
import PhotosBathRoom from './PhotosBathRoom';
import PhotosInterior from './PhotosInterior';
import PhotosOutDoor from './PhotosOutDoor';

function CustomTabPanel(props) {
    const { children, value, index, ...other } = props;

    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            id={`simple-tabpanel-${index}`}
            aria-labelledby={`simple-tab-${index}`}
            {...other}
        >
            {value === index && (
                <Box sx={{ p: 3 }}>
                    <Typography>{children}</Typography>
                </Box>
            )}
        </div>
    );
}

CustomTabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.number.isRequired,
    value: PropTypes.number.isRequired,
};

function a11yProps(index) {
    return {
        id: `simple-tab-${index}`,
        'aria-controls': `simple-tabpanel-${index}`,
    };
}

export default function TabsGallery() {
    const [value, setValue] = React.useState(0);

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };

    return (
        <Box sx={{ width: '100%' }}>
            <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
                <Tabs value={value} onChange={handleChange} aria-label="basic tabs example">
                    <Tab label="BackYard" {...a11yProps(1)} />
                    <Tab label="BathRoom" {...a11yProps(2)} />
                    <Tab label="Interior" {...a11yProps(3)} />
                    <Tab label="OurDoor" {...a11yProps(0)} />
                </Tabs>
            </Box>
            <CustomTabPanel value={value} index={0}>
                <PhotosBackYard />
            </CustomTabPanel>
            <CustomTabPanel value={value} index={1}>
                <PhotosBathRoom/>
            </CustomTabPanel>
            <CustomTabPanel value={value} index={2}>
                <PhotosInterior/>
            </CustomTabPanel>
            <CustomTabPanel value={value} index={3}>
                <PhotosOutDoor/>
            </CustomTabPanel>
        </Box>
    );
}