import React from 'react'
import { 
  Box, 
  Stack, 
  styled, 
  Typography,
} from '@mui/material'
import Link from '@mui/material/Link';
import FooterTitle from './FooterTitle'
import FooterLink from './FooterLink'
import FacebookIcon from '@mui/icons-material/Facebook';

const Footer = () => {

  const StackColumn = styled(Stack) (() => ({
    flexDirection: 'column',
    justifyContent: 'flex-start',
    alignItems: 'center',
    flex: 1,
    gap: 8,
    textAlign: 'center',
  }));

  const BoxRow = styled(Box) (({ theme }) => ({
    display: 'flex',
    flexDirection: 'row',
    backgroundColor: 'skyblue',
    flex: 1,
    [theme.breakpoints.down('sm')]: {
      flexDirection: 'column',
      gap: 30,
    }
  }));

  return (
    
    <BoxRow 
    component = 'footer'
    sx={{
      py: 4,
      px: 2,
    }}
    >
      
      <StackColumn>
        <FooterTitle text={'address'} />
        <FooterLink 
          text={'Mandan, ND 58554, USA'} 
        />
        <FooterLink 
          text={'+1701-805-5679'} 
        />
        <FooterLink 
          text={'ndconstructionandconcrete@gmail.com'} 
        />
      </StackColumn>
      
      <StackColumn>
        <FooterTitle text={'our services'} />
        <FooterLink text={'Interior design'} />
        <FooterLink text={'Backyard'} />
      </StackColumn>


      <StackColumn>
        <FooterTitle text={'NDConstructionAndConcrete'} />
        <Stack 
        direction='row' 
        width= '70px'
        maxWidth='100%'
        justifyContent='space-between'
        >
 
          <Link href="https://www.facebook.com/NDConstructionAndConcrete"variant="body2" 
          sx={{
            color: '#414141',
            "&:hover": {
              color: '#1c2859',
            }
          }}
          >
            <FacebookIcon />
          </Link> 
        </Stack>
        <Typography 
        variant='caption'
        component='p' 
        >
          &copy; 2024 NDConstructionAndConcrete Inc.
        </Typography>
        <Typography>
          
        </Typography>
      </StackColumn>
    </BoxRow>
  )
}

export default Footer