import React from 'react'
import {
    AppBar,
    Toolbar,
    Box,
    List,
    ListItem,
    Typography, 
    styled,
    ListItemButton,
    ListItemText,
} from '@mui/material';
import DrawerItem from './DrawerItem';
import { Link } from 'react-router-dom';


const StyledToolbar = styled(Toolbar) ({
    display: 'flex',
    justifyContent: 'space-between',
    
});

const ListMenu = styled(List)(({ theme }) => ({
    display: 'none',
    
    [theme.breakpoints.up("sm")] : {
        display: "flex",
    },
}));

const itemList = [
    {
      text: "Home",
      to: "/" 
    },
    {
      text: "Services",
      to: "/Services"
    },
    {
        text: "Contact",
        to: "/contact"
    }
];


const Navbar = () => {
    
    return (
        <AppBar 
        component="nav" 
        position="sticky"
        sx={{ 
            backgroundColor: 'rgba(225, 225, 225, 0.6)', 
        }}
        elevation={0}
        >
            <StyledToolbar>
                <Typography
                variant="h5"
                component="h2"
                color={'blue'}
                fontStyle={'bold'}
                >
                    ND Construction And Concrete
                </Typography>
                <Box sx={{display: { xs: 'block', sm: 'none' } }}>
                    <DrawerItem /> 
                </Box>
                <ListMenu>
                    {itemList.map( ( item ) => {
                        const { text } = item;
                        return(
                            <ListItem key={text}>
                                <ListItemButton component={Link} to={item.to}
                                sx={{
                                    color: '#000000',
                                    "&:hover": {
                                        backgroundColor: 'transparent',
                                        color: '#1E90FF',
                                    }
                                }}
                                >
                                    <ListItemText primary={text} />
                                </ListItemButton>
                            </ListItem>
                        )
                    })}
                </ListMenu>
            </StyledToolbar>
        </AppBar>
    )
}

export default Navbar;
